import React, { useState } from "react";
import Navbar from './components/Navbar'
import Poster from './components/Poster'
import Banner from './components/Banner'
import Input from './components/Input'
import './App.css'

const cheerio = require("cheerio");
const axios = require("axios");

let id = 1;

function App() {
  const [posters, setPosters] = useState([]);


  function addPoster(title) {
    const search = "https://myanimelist.net/search/all?cat=all&q=" + title;
    // console.log(search);
    
    axios(search).then(res => {
      const html = cheerio.load(res.data);
      if (!html.text().includes("Your keyword is too short.")){
        const article = html('article').first()
        const a = article.find('div').first().find('a');
        const name = article.find('div').first().find('div.information').find('a').first().text()
        const href = a.attr('href');
        const img = a.find('img').attr('data-src').replace('/r/100x140', '');
        // console.log(img)
        // console.log(href);
  
        const newPoster = { id, name, href, img };
  
        setPosters([newPoster, ...posters]);
        id += 1;
      }
    
    })
  }


  function deletePoster(id) {
    const updatedPosts = posters.filter((poster) => poster.id !== id);
    setPosters(updatedPosts);
  }

  return (
    <div className="App">
      <Navbar />
      <div className="panel">
        <Banner img="https://i2.wp.com/akibatan.com/wp-content/uploads/2015/12/gintama-manga-will-enter-final-arc-next-year.jpg?ssl=1" />

        <Input addPoster={addPoster} />

        <div className="anime-wrapper">
          {posters.map((poster) => (
            <Poster id={poster.id} name={poster.name} href={poster.href} img={poster.img} key={poster.id} deletePoster={deletePoster} />
          ))}
        </div>
      </div>
    </div>
  );
}

export default App;
